.overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.6);
  z-index: 999;

  .content-container {
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    box-shadow: 0px 4px 12px 5px rgba(0, 0, 0, 0.1);
    border: 1px solid #00b7c3;
    margin: auto;
    height: 15em;
    width: 15em;
    .content {
      display: flex;
      gap: 2em;
      flex-direction: column;
      .title {
        text-align: center;
        font-size: 1.5em;
      }
    }
  }
}
